import { LightNav } from './../../../light/light-providers/providers/lightNav';
import { LightHttp, LightJsonUtil, LightWriterService, LightLocalCache } from './../../../light/light-out/light-out.module';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { IonicPage, NavController, NavParams, App } from 'ionic-angular';

@IonicPage({
  segment: 'work-detail'
})
@Component({
  selector: 'page-work-detail',
  templateUrl: 'work-detail.html',
})
export class WorkDetailPage implements OnInit, OnDestroy{

  // 对应的单项code值
  code: string;
  //流程类型
  JobType: string;
  //当前环节名称
  curStepTitle: string;
  //底部是否显示
  showFlag: boolean;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public lightHttp: LightHttp,
    public lightWriterService: LightWriterService,
    public lightNav: LightNav,
    public lightLocalCache: LightLocalCache
  ) {
  }

  public ngOnInit(): void {
    // TODO:如果是浏览器前进操作进入的页面，导航参数如何还原？
    console.log(location.href)
    if (this.navParams.data.item) {
      console.log(this.navParams.data)
      this.lightLocalCache.setObject('workDetailPageParams', this.navParams.data)
    } else {
      console.log('get from local')
      this.navParams.data = this.lightLocalCache.getObject('workDetailPageParams')
    }
    let data = this.navParams.data;
    this.code = data.item.CODE;
    this.JobType = data.item.JobType;
    this.showFlag = data.item.showFlag;

    this.lightHttp.get("Flow/GetCurStep", { objectID: this.code }).then((data) => {
      var curStepData = LightJsonUtil.toObject(data) as any;
      this.curStepTitle = (JSON.stringify(curStepData) === '{}') ? "已经归档" : curStepData.Title;
    }).catch(err=>{
      console.log(err);
    });
  }

  //流程日志
  lookFlow() {
    this.navCtrl.push("WorkFlowChartPage", { objectID: this.code });
  }

  ngOnDestroy() {
    // this.navCtrl.popToRoot()
  }

}




